const $siteList = $('.siteList')
const $lastLi = $siteList.find('li.lastLi')
let dataUrl = localStorage.getItem('dataUrl')
dataUrl = JSON.parse(dataUrl)
const hashMap = dataUrl || [{
  url: 'https://juejin.cn/',
  name: 'ÊéòÈáë'
},
  {
    url: 'https://nodejs.org/en/',
    name: 'Node'

  },
  {
    url: 'https://react.docschina.org/',
    name: 'React'
  },
  {
    url: 'https://cn.vuejs.org/',
    name: 'Vue'
  },
  {
    url: 'https://www.zhihu.com/',
    name: 'Áü•‰πé'
  },
  {
    url: 'https://www.bilibili.com/',
    name: 'ÂìîÂì©ÂìîÂì©'
  }
]
const simplify = (url) => {
  return url.replace('http://', '')
    .replace('https://', '')
    .replace('www.', '')
    .replace(/\/.*/, '') // Âà†Èô§ / ÂêéËæπÁöÑÂÜÖÂÆπ
}


// const findIcon = (url) => {
//   return "https://www.google.com/s2/favicons?domain=" + url + "";
// };


const render = () => {
  $siteList.find('li:not(.lastLi)').remove()
  hashMap.forEach((item, index) => {
    const $li = $(`
    <li>
     <div class="site">
       <div class="logo">
          <svg class="icon">
              <use xlink:href="#icon-${item.url[8]}"></use>
           </svg>
       </div>
       <div class="close">
           <svg class="icon">
               <use xlink:href="#icon-close"></use>
            </svg>
        </div>
       <span class="siteName">${item.name}</span></span>
     </div> 
   </li>
    `).insertBefore($lastLi)
    // console.log(simplify(item.url)[0])
    const $close = $li.find('.close')
    // ÂÆö‰πâPCÁ´ØÂÆöÊó∂Âô®
    let tigger = null
    // ÂÆö‰πâÁßªÂä®Á´ØÂÆöÊó∂Âô®
    let timeOutEvent = null
    // ÂÆö‰πâÈïøÊåâ Ôºå Áî®Êù•Âà§Êñ≠ÊòØÂê¶‰∏∫ÈïøÊåâ‰∫ã‰ª∂
    // ‰∏çÂÆö‰πâ‰ºöÂá∫Áé∞BUGÔºöÊó†ËÆ∫ÁÇπÂáªÊàñËÄÖÈïøÊåâÔºåÈÉΩ‰ºöÊâßË°åtouchedÂÜÖÁöÑÊñπÊ≥ïÔºåÂç≥ÈïøÊåâÁÇπÂáªÈÉΩ‰ºöÂéªË∑≥ËΩ¨È°µÈù¢„ÄÇ
    let longClick = null
    // Âà†Èô§Á´ôÁÇπ
    let deleteLi = function (e) {
      e.stopPropagation()
      hashMap.splice(index, 1)
      render()
    }
    $li.on('click', () => {
      window.open(item.url)
    })

    // PCÁ´Ø ÊòæÁ§∫ÈöêËóèÂà†Èô§ÊåâÈíÆ
    $li.on('click', '.close', deleteLi)
    $li.hover(() => {
      tigger = setTimeout(() => {
        $close.show(500)
      }, 300)
    }, () => {
      clearTimeout(tigger)
      $close.hide(300)
    })

    // ÁßªÂä®Á´ØÂà†Èô§Êìç‰Ωú
    $li.on({
      // ÊâãÊåáÂºÄÂßãËß¶Êë∏Êó∂ÔºåËÆæÁΩÆÂÆöÊó∂Âô®„ÄÇ
      touchstart: function (e) {
        longClick = 0
        timeOutEvent = setTimeout(() => {
          $close.show(500)
          $li.on('click', '.close', deleteLi)
          longClick = 1
        }, 500)
      },
      // Â¶ÇÊûúÊâãÊåáÊªëÂä®Âè™ÊòØËØ¥ÊòéÁî®Êà∑‰∏çÊÉ≥ÈïøÊåâÂè™ÊÉ≥ÊªëÂä®„ÄÇ ÊâÄ‰ª•Ë¶ÅÂèñÊ∂àÂÆöÊó∂Âô®Âπ∂ËøòÂéü„ÄÇ
      // Â¶ÇÊûú‰∏çËøòÂéüÔºåËøîÂõûÁöÑÂÆöÊó∂Âô®ÁöÑÂÄºÊòØ‰∏çÂêåÁöÑ„ÄÇ
      touchmove: function (e) {
        clearTimeout(timeOutEvent)
        timeOutEvent = 0
        e.preventDefault()
      },
      // Â¶ÇÊûúÈïøÊåâÊ≤°ÊúâË∂ÖËøá500ms ÔºåÊâãÊåáÁ¶ªÂºÄÂ∞±ÂèñÊ∂àÂÆöÊó∂Âô®ÔºåÊâßË°åÁÇπÂáª‰∫ã‰ª∂„ÄÇ
      touchend: function (e) {
        clearTimeout(timeOutEvent)
        if (timeOutEvent !== 0 && longClick === 0) {
          $li.on('click', '.close', deleteLi)
        }
      }
    })
  })
}

render()


$('.addButton')
  .on('click', () => {
    let url = window.prompt('ËØ∑Ê∑ªÂä†‰Ω†ÊÉ≥Ë¶ÅÁöÑÁΩëÂùÄ')
    let name = window.prompt('ËØ∑ËæìÂÖ•Ê∑ªÂä†ÁΩëÂùÄÁöÑÂêçÂ≠ó')
    if (url.indexOf('http') !== 0) {
      url = 'https://' + url.trim()
    }
    // console.log(url);
    hashMap.push({
      url: url,
      name: name
    })
    render()
  })


window.onbeforeunload = () => {
  console.log('È°µÈù¢Á¶ªÂºÄ‰∫Ü');
  // Âõ†‰∏∫localStorageÂè™ËÉΩÂ≠òÂ≠óÁ¨¶‰∏≤

  const string = JSON.stringify(hashMap)
  localStorage.setItem('dataUrl', string)
}